<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Otoritas',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Otoritas'+ ' ' +this.$route.params.action,
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			aksiEdit: false,
      		aksiSave: false,
			alert:false,
			dataotoritaswebsite: [],
			modelotoritaswebsite:"",
		}
	},
	mounted() {
		console.log("alamat", Utils.Host())
		this.Getdata()
		this.Getfilter()
		if (this.$route.params.action === 'tambah') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'edit') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'detail') {
		this.aksiEdit = false
		this.aksiSave = false
    }
  },
  computed: {
  },
  methods: {
    async Getdata() {
      try {
        const action = this.$route.params.action
        if (action === 'detail' || action === 'edit') {
          let response = await axios.get(this.api+"/otoritas/edit/"+this.$route.params.id, Utils.authHeader());
          this.data = response.data;
        }
      } catch (err) {
          console.error(err);
      }
    },
    async openEdit(id) {
        document.location = '/otoritasaction/edit/' + id
        // location.reload();
    },
	async Getfilter() {
        try {
			let response = await axios.get(this.api+"/otoritaswebsite/index", Utils.authHeader());
    		this.dataotoritaswebsite = response.data;
        } catch (err) {
            console.error(err);
        }
      },
    
	validation() {
      if (!this.data.f_otoritas) {
          return { code: 500, message: 'Hak Akses Data ini harus diisi' }
      }  else {
          return { code: 200, message: '' }
      }
    },
    async onSave() {
        const validation = this.validation()
        if (validation.code === 200) {
        var res = null
        const action = this.$route.params.action;
		const target = new URLSearchParams();
		target.append("f_otoritas", this.data.f_otoritas);
		target.append("f_otoritas_website", this.data.modelotoritaswebsite.f_kode);
		target.append("fn_otoritas_website", this.data.modelotoritaswebsite.f_nama);
        if (action === 'tambah') {
            res = await axios.post(this.api+"/otoritas/add", target, Utils.authHeader());
        } else {
            res = await axios.put(this.api+"/otoritas/edit/"+ this.$route.params.id, target, Utils.authHeader());
        }
        if (res.data.code === 200) {
			this.alert = true
			this.message = res.data.message
            document.location = '/otoritasaction/detail/' + res.data.data.f_otoritas_id;
            // this.Getdata();
            this.aksiEdit = !this.aksiEdit
            this.aksiSave = false
        }  else {
			this.alert = true
			this.message = res.data.message
        }
        } else {
			this.alert = true
			this.message = validation.message
        }
    },
  },
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>

									<br>&nbsp;&nbsp;
									<br>
									<div class="table-responsive">
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Hak Akses"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_otoritas"
										type="text"
										required
										placeholder="Hak Akses"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nama Website"
										label-for="input-horizontal"
									>
									<b-form-select v-model="data.modelotoritaswebsite">
										<option v-for='(value, key) in dataotoritaswebsite' :value='value'>{{value.f_nama}}</option>
									</b-form-select>
								</b-form-group>	
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label-for="input-horizontal"
									>
									<b-button variant="success" @click="onSave(data.f_otoritas_id)"  v-if="aksiEdit">Simpan Data</b-button>
									&nbsp;
									<b-button variant="danger" @click="openEdit(data.f_otoritas_id)"  v-if="!aksiEdit">Edit Data</b-button>
									</b-form-group>									

									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
